.grey {
    position: relative;
    height: 30px;
    width: 100%;
    span {
        display:block;
        position: absolute;
        height: 100%;
        width: 100%;
        background: #ccc;
        left: 0;
        top:0;
    }
}

$config-grid-enabled: true;

$o-grid-gutter-width: 20px;

@if $config-grid-enabled {
    .o-grid {

        &__wrap {
            display: flex;
            flex-wrap: wrap;
            flex: 0 1 auto;
            margin:  0;
            padding: 0;
            list-style: none;
            margin-left: -$o-grid-gutter-width / 2;
            margin-bottom: -$o-grid-gutter-width;
            margin-right: -$o-grid-gutter-width / 2;
        }

        &__item {
            flex-grow: 1;
            padding-right: $o-grid-gutter-width / 2;
            padding-bottom: $o-grid-gutter-width;
            padding-left: $o-grid-gutter-width / 2;
        }

        &--full-screen {
            overflow-x: hidden;
            max-width: 100%;
        }
    }
}


@mixin grid($columns: 12) {

    @for $i from 1 through $columns {
        &-#{$i}\/#{$columns} {
            flex-basis: $i * 100% / $columns;
            max-width: $i * 100% / $columns;
            min-width: 0;
        }
    }

    @each $breakpoint, $value in $mq-breakpoints {
        @include breakpoint($breakpoint) {
            @for $i from 1 through $columns {
                &-#{$i}\/#{$columns}\@#{$breakpoint} {
                    flex-basis: $i * 100% / $columns;
                    max-width: $i * 100% / $columns;
                    min-width: 0;
                }
            }
        }
    }
}

.u {
    @include grid();
}


